import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'

Vue.use(Vuex)
axios.defaults.baseURL = 'http://api.tvmaze.com/'

export const store = new Vuex.Store({
  state: {
    shows: [],
    query: '',
  },
  mutations: {
    updateShows(state, shows) {
      state.shows = shows
    },
    updateQuery(state, query) {
      state.query = query
    },
  },
  actions: {
    getFilteredShows(context, data) {
      return axios
        .get(`search/shows?q=${data}`)
        .then(response => {
          context.commit('updateShows', response.data)
          context.commit('updateQuery', data)
          return response
        })
        .catch(error => {
          console.log({ error })
        })
    },
  },
})
